const TabletSec = () => {
    return (
        <div className='container'>
        <div className='flex-items'>
            <Fade  left>

            <div className='text' >
                <h3>
                Counseling and pedagogical and socio-emotional support
                </h3>
            </div>
            </Fade>
            <Fade right>
            <div className='tablet-image'>
                <img src={Assist} alt='tablet'></img>
            </div>
            </Fade>
        </div>
    </div>
);
};



export default TabletSec;
