import React, { useEffect, useState } from 'react';
import data from './sliderData';
// import Fade from 'react-reveal/Fade';
import AOS from 'aos';
import {FiChevronLeft,FiChevronRight, FiChevronsLeft, FiChevronsRight} from "react-icons/fi";
import "./Slider.css";

function Slider() {
    const [info,setInfo] = useState(data);
    const [index, setIndex] = useState(0);
    useEffect ( ()=>{
        let lastIndex = info.length -1;
        if(index < 0){
            setIndex(lastIndex)
        }
        if(index > lastIndex){
            setIndex(0)
        }
    },[index,info])

    // make autoplay
    useEffect (()=>{

      let autoplay =  setInterval(() => {
            setIndex(index + 1);
        }, 50000);
        return ()=>clearInterval(autoplay)
    },[index])



    return (
        <section>

            <div className='slider-section'>
                    <div className='section-center'>
                            {
                                info.map((image,imageIndex)=> {
                                    const {id,images,title,text,buttonName} = image;
                                    let position = "nextSlide" ;
                                    if(imageIndex === index){
                                        position = "activeSlide";
                                    }
                                    if(imageIndex === index - 1 || (index === 0 && imageIndex === info.length - 1)){
                                        position = "lastSlide";
                                    }
                                    return(
                                        <article  className={position} key={id}>

                                        <img src={images} alt="sliderimg"/>
                                        <div className='text-box'>
                                            {/* <Fade left> */}

                                        <h4 data-aos="fade-left">{title}</h4>
                                        <p>{text}</p>
                                        <button><a href='https://www.google.com/imghp?hl=fa'>{buttonName}</a></button>
                                            {/* </Fade> */}


                                        </div>

                                        </article>
                                    )
                                })
                            }
                            <button className='next btn' onClick={()=> setIndex(index+1)}><FiChevronRight/></button>
                            <button className='prev btn'  onClick={()=> setIndex(index-1)}><FiChevronLeft/></button>
                    </div>
            </div>
        </section>
    );
}

export default Slider;